}

.main-container .table-striped > tbody > tr:nth-of-type(odd) {
  background-color: transparent;
}

.main-container .table-hover > tbody > tr > td.active:hover,
.main-container .table-hover > tbody > tr > th.active:hover,
.main-container .table-hover > tbody > tr.active:hover > td,
.main-container .table-hover > tbody > tr:hover > .active,
.main-container .table-hover > tbody > tr.active:hover > th {
  background-color: transparent;
}

.main-container .table tr td a {
  color: #000;
}

.main-container .highlighted_bottom {
  margin-top: 2em;
}

#footer {
  background-color: #EFF0F2;
  padding: 20px 80px 20px 53px;
}

.frontPageClock {
  max-width: 200px;
}

.frontPageClock .dateContainer {
  text-transform: lowercase;
  color: #777;
  font-size: 0.8rem;
}

.frontPageClock .timeContainer {
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.frontPageClock .timeContainer:before {
  font-family: 'icomoon', sans-serif !important;
  font-size: 2rem;
  line-height: 2rem;
  content: "\e921";
  display: inline-block;
  margin-right: 5px;
}

.btn-info:focus,
.btn-info.focus,
.btn-info:active:hover,
.btn-info.active:hover,
.open > .dropdown-toggle.btn-info:hover,
.btn-info:active:focus,
.btn-info.active:focus,
.open > .dropdown-toggle.btn-info:focus,
.btn-info:active.focus,
.btn-info.active.focus,
.open > .dropdown-toggle.btn-info.focus {
  background-color: #1D1D1B;
}

.region-highlighted-bottom {
  display: flex;
  flex-wrap: wrap;
  gap: 26px;
}

.region-highlighted-bottom > section {
  width: calc(50% - 13px);
}

.region-highlighted-bottom > section .block-content {
  background-color: #EFF0F2;
  border-radius: 6px;
  text-align: center;
  text-transform: uppercase;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #484848;
  position: relative;
}

.region-highlighted-bottom > section .block-content h1,
.region-highlighted-bottom > section .block-content h2,
.region-highlighted-bottom > section .block-content h3 {
  padding: 0;
  margin: 0 0 10px 0;
}

.region-highlighted-bottom > section .block-content h3 {
  font-size: 20px;
  font-weight: 700;
}

.region-highlighted-bottom > section .block-content p {
  font-size: 12px;
}

.region-highlighted-bottom > section .block-content a {
  text-decoration: none;
}

.region-highlighted-bottom > section .block-content:after {
  font-family: 'icomoon', sans-serif !important;
  content: "\e90c";
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 1.5em;
}

.field.field-name-field-photos {
  column-count: 3;
}

.field.field-name-field-photos .field-item {
  margin-bottom: 1em;
}

.field-name-field-youtube {
  margin-bottom: 1em;
}

body.page-node .node header .submitted {
  font-size: 1.3em;
  font-weight: 600;
}

body.page-node .node footer .field-name-field-tags {
  padding: 1em 0;
}

body.page-node .node footer .field-name-field-tags .field-items .field-item {
  display: inline-block;
}

body.page-node .node footer .field-name-field-tags .field-items .field-item a {
  display: inline-block;
  border-radius: 25px;
  background-color: #EFF0F2;
  padding: 6px 15px;
  line-height: 1;
  margin-right: 4px;
  font-size: 14px;
  font-weight: 600;
  color: #9A9A9A;
}

body.page-node .node footer .field-name-field-tags .field-items .field-item a:hover {
  text-decoration: none;
  color: #6D727C;
}

body.page-node .node .field {
  font-size: 16px;
}

body.page-node .node .field-name-body.field-type-text-with-summary {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 1em;
}

body.page-node .node .field-name-body.field-type-text-with-summary img {
  margin: 1em;
  max-width: 100%;
  height: auto !important;
}

body.page-node .node .field-name-body.field-type-text-with-summary ol,
body.page-node .node .field-name-body.field-type-text-with-summary ul {
  display: block;
  clear: both;
}

body.page-node .node .field-name-field-image {
  max-width: 48%;
  float: left;
  margin-right: 20px;
  margin-bottom: 10px;
}

body.page-node .node .children .views-field-title a {
  font-weight: 700;
  font-size: 1.5em;
  display: inline-block;
  padding: 0 0 0.6em 0;
}

body.page-node .node .children .views-field-title a:after {
  font-family: 'icomoon', sans-serif !important;
  content: "\e90c";
  font-size: 0.8em;
  display: inline-block;
  padding-left: 0.5em;
}

#block-views-posters-block-1 .view-content > .views-row {
  background-color: var(--o-gray);
  border: none;
}

#block-views-posters-block-1 .view-content > .views-row:nth-child(1) .views-field,
#block-views-posters-block-1 .view-content > .views-row:nth-child(1) a, #block-views-posters-block-1 .view-content > .views-row:nth-child(2) .views-field,
#block-views-posters-block-1 .view-content > .views-row:nth-child(2) a {
  color: #fff;
}

#block-views-posters-block-1 .view-content > .views-row:nth-child(1) {
  background-color: var(--o-red);
}

#block-views-posters-block-1 .view-content > .views-row:nth-child(2) {
  background-color: var(--o-blue);
}

.pagination > li > a,
.pagination > li > a:hover,
.pagination > li > a:focus,
.pagination .pagination > li > span,
.pagination .pagination > li > span:hover,
.pagination .pagination > li > span:focus {
