}

.pagination > .active > a, .pagination > .active > a:hover, .pagination > .active > a:focus,
.pagination > .active > span,
.pagination > .active > span:hover,
.pagination > .active > span:focus {
  color: #fff;
  background-color: #aaa;
  border-color: #888;
}

@media (min-width: 1400px) {
  .container {
    width: 1400px;
  }
}
